import React, { Component } from 'react';
import Button from "./common/Button";
import Project from "./common/Project";
import PageHeader from "./common/PageHeader";
import {config} from "../Constants";

export class UnderImplement extends Component {
    state = {
        projects: [],
        fillter_Projects:[]
    }

    componentDidMount() {
        this.fetchData();
    }

    fetchData() {
        // fetch(`${config.API_URL}api/projects/`)
        fetch(`${config.API_URL}api/projects/`)
            .then(response => response.json())
            .then(data =>{
                this.setState({ projects: data })
this.setState({ fillter_Projects : this.state.projects})
console.log(data)
            }
            )
    }

    // this function will fillter the projects based on which tag( project status ) user click
    handleClick = ({ currentTarget: element }) => {
        let tag = element.textContent;
        let elements = document.getElementsByClassName('tags_clicked');
        for (let i = 0; i < elements.length; i++) {
            elements[i].classList.remove('tags_clicked')
        }
        element.classList.add('tags_clicked')
        let projects = this.state.projects;
        if (tag !== 'كل المشاريع') {
            let fillter_projects = projects.filter(p => p.StatusAr === tag);
            this.setState({ fillter_Projects: fillter_projects })
        }
        else 
        this.setState({fillter_Projects : projects})
    }

    render() {
        const projects = this.state.fillter_Projects;

        return (
            <div className="text-right" style={{ backgroundColor: 'white', padding: '10px', paddingTop: '0', direction: 'rtl' }}>
                <div className="container">
                    <PageHeader />
                    <div id="tags_contanier">

                        {/* tags */}
                        <h3 id="tags_title">يمكنك تصفية  المشاريع باختيار حالتها </h3>
                        <Button classname="tag whiteColor all_projects" label='كل المشاريع' handleClick={this.handleClick}></Button>
                        <Button classname="tag whiteColor done_projects" label='منجز' handleClick={this.handleClick}></Button>
                        <Button classname="tag whiteColor prosses_projects" label='تحت التنفيذ' handleClick={this.handleClick}></Button>
                        <Button classname="tag whiteColor design_projects" label='تحت التخطيط' handleClick={this.handleClick}></Button>
                    </div>
                    {/* projects */}
                    {projects.length === 0 ? <p id="no_services">لاتوجد أي مشاريع هنا  ..</p> :
                    <div className="row">
                        {projects.map((project, index) => (
                            <div className="col-md-6 col-lg-4 service_continer" key={project + index}>
                                <Project
                                    index={index}
                                    backgroundImg={project.BackgroundProject}
                                    status={project.StatusAr}
                                    name={project.NameAr} />
                            </div>))
                        }
                    </div>
    }
                </div>
            </div>
        )
    }
}

export default UnderImplement;