import React, { Component } from 'react';
import PageHeader from "./common/PageHeader";
import Button from './common/Button';
import {config} from "../Constants";

export class ProjectPage extends Component {
    state = {
        project: {}
    }

    componentDidMount() {
        this.fetchData();
    }

    // this will fetchData from Api
    fetchData() {
        var { id } = this.props.match.params;
        fetch(`${config.API_URL}api/projects/${id}/`)
            .then(response => response.json())
            .then(data =>{
                this.setState({ project: data })
                console.log(data)}
            )
    }

    render() {
        const project = this.state.project;
        return (
            <>
                <div className="text-right" style={{ backgroundColor: 'white', padding: '10px', paddingTop: '0', direction: 'rtl' }}>
                    <div className="container">
                        <PageHeader />
                        {/* project */}
                        <div className="row">
                            <div className="project_box fade" >
                                <h2 className="whiteColor">{project.NameAr} </h2>
                                <p className="whiteColor mt-4"><strong>التصنيف :</strong> {project.ServiceTypeAr}</p>
                                {project.UrlProject !== null ? <Button label="معاينة المشروع" classname="project_Btn whiteColor" handleClick={() => window.open(project.UrlProject)} /> : null}
                            </div>

                        </div>

                    </div>



                </div >
            
              { project.UrlAdv !== null ? <><div id="video_box">
                    <h1 className="whiteColor mb-5"> خدمة {project.ServiceTypeAr} </h1>
                   { <object width="70%" height="350" data={project.UrlAdv} title="YouTube video player" ></object>}
                </div>
                <div style={{ backgroundColor: 'white', padding: '20px', textAlign: 'center' }}>
                    <div className="container">
                        <Button label="تقدم بطلبك الآن" classname="submit_btn whiteColor" handleClick={() => this.props.history.push('/order')} />
                    </div>
                </div> </>: null}
            </>
        )
    }
}

export default ProjectPage;