import React, { Component } from 'react';
import PageHeader from "./common/PageHeader";
import { getServices } from '../Services';
import Heading from './common/Heading';
import {config} from "../Constants";

export class OurProjects extends Component {
    state = {
        services: getServices(),
        projects: {
            3: 0,
            5: 0,
            1: 0,
            2: 0,
            4: 0,
            6: 0
        }
    }

    componentDidMount() {
        this.fetchData();
    }
    // this will fetchData from Api
    fetchData() {
        fetch(`${config.API_URL}api/projects/`)
            .then(response => response.json())
            .then(data =>
                {
                this.setState({
                    projects: {
                        3: data.filter(d => d.ServiceTypeEn === "Design").length,
                        5: data.filter(d => d.ServiceTypeEn === "Marketing & Consulting").length,
                        1: data.filter(d => d.ServiceTypeEn === "Production").length,
                        2: data.filter(d => d.ServiceTypeEn === "Exhibitions & Events").length,
                        4: data.filter(d => d.ServiceTypeEn === "Branding").length,
                        6: data.filter(d => d.ServiceTypeEn === "Mobile Apps & Web").length
                    }
                })
console.log(this.state.projects.production)}
            )
    }

    // this function will redirct you to the projects page of this section 
    handleClick = (service) => {
        this.props.history.push(`/projects/${service.name_en.replace(/\s/g, '-')}`)
    }

    // this function returns how much projects this section have
    getNum = (service) => {
        let id = service.id
        let num = this.state.projects[id];
        console.log ( "num : "+ num  + " name : " + id )
        console.log ( this.state.projects)
        return num;
    }

    render() {
        const { services } = this.state;

        return (
            <div className="text-right" style={{ backgroundColor: 'white', padding: '10px', paddingTop: '0', direction: 'rtl' }}>
                <div className="container">
                    <PageHeader />
                    {services.length === 0 ? <p id="no_services">لا تتوفر أي أعمال حالياً ..</p> :
                    <div className="row">
                        {services.map((service, index) => (
                            <div className="col-md-6 col-lg-4 service_continer" key={service + index}>
                                <Heading
                                    index={index}
                                    name={service.name_ar}
                                    number={this.getNum(service)}
                                    handleClick={() => this.handleClick(service)} />
                            </div>))
                        }
                    </div>
    }
                </div>
                    
            </div>
        )
    }
}

export default OurProjects;