import React, { Component } from 'react';
import Project from "./common/Project";
import PageHeader from "./common/PageHeader";
import {config} from "../Constants";

export class Projects extends Component {
    state = {
        projects: []
    }

    componentDidMount() {
        this.fetchData();
    }

    // this will fetchData from Api
    fetchData() {
        var { name } = this.props.match.params;
        name = name.replace(/-/g, ' ');
        fetch(`${config.API_URL}api/projects/`)
            .then(response => response.json())
            .then(data => {
                let fillter_Projects = data.filter(p => p.ServiceTypeEn === name);
                this.setState({ projects: fillter_Projects })
            }
            )
    }

    // this function will redirct you to the project page 
    handleClick = (id) => {
        var { name } = this.props.match.params;
        console.log(id)
        this.props.history.push(`/projects/${name}/${id}`)
    }


    render() {
        const projects = this.state.projects;
        return (
            <div className="text-right" style={{ backgroundColor: 'white', padding: '10px', paddingTop: '0', direction: 'rtl' }}>
                <div className="container">
                    <PageHeader />
                    {/* projects */}
                    {projects.length === 0 ? <p id="no_services">لم يتم تنفيذ أي مشروع تحت هذه الخدمة  ..</p> :
                    <div className="row">
                        {projects.map((project, index) => (
                            <div className="col-md-6 col-lg-4 service_continer" key={project + index}>
                                <Project
                                    index={index}
                                    backgroundImg={project.BackgroundProject}
                                    name={project.NameAr}
                                    handleClick={() => { this.handleClick(project.id) }}
                                />
                            </div>))
                        }
                    
                    </div>
    }

                </div>
            </div>
        )
    }
}

export default Projects;