import React from 'react';

/*
this componet will represent service projects section image
while:
name : is the section name
number : is the number of projects section have
handleClick : is what happened if you click in section
*/
const Heading = ({ index, name, number, handleClick }) => {

    return (
        <div className="project fade" style={{
            backgroundImage: "linear-gradient(to top right, #C92C37 , #343A40)",
            animationDelay: `${index * .5}s`,
            cursor: 'pointer'
        }}
            onClick={handleClick}>
            <div className="home_num">{number}</div>
            <h3 className="project_name whiteColor">{name}</h3>
        </div>
    );
};

export default Heading;