import React from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
    faTwitterSquare,
    faSnapchatSquare,
    faWhatsappSquare,
    faInstagramSquare,
    faYoutubeSquare,
    
}
    from '@fortawesome/free-brands-svg-icons'

// footer container 
export function Footer() {

    return (
        <div id='footer' >
            <div className="container" >
                <div className="row text-center">
                    <h3 className="col-md-8 text-md-left" id="copywrite">جميع الحقوق محفوظة ، مؤسسة ريد فريم &copy; 2021 </h3>
                    <div className="col-md-4 text-md-right" >
                        {
                            // share buttons 
                        }
                        <FontAwesomeIcon className="icon" icon={faTwitterSquare} onClick={()=>{window.open()}}/>
                        <FontAwesomeIcon className="icon" icon={faSnapchatSquare} onClick={()=>{window.open('https://www.snapchat.com/add/redframesa')}} />
                        <FontAwesomeIcon className="icon" icon={faWhatsappSquare} onClick={()=>{window.open('https://wa.me/message/IWZWBOVLEJZKB1')}}/>
                        <FontAwesomeIcon className="icon" icon={faInstagramSquare} onClick={()=>{window.open('https://www.instagram.com/redframesa')}}/>'
                        <FontAwesomeIcon className="icon" icon={faYoutubeSquare} onClick={()=>{window.open('https://m.youtube.com/channel/UCRzMWNN6TQ3Y_WCSfXxx5BA')}}/>
                
                    
                    </div>
                </div>
            </div></div >

    );

}

export default Footer;